<!-- ËÆ¢ÂçïÂïÜÂìÅ --> <!-- ËÆ¢ÂçïÁºñÂè∑ -->  <!-- ËÆ¢ÂçïÂïÜÂìÅÊï∞ÊçÆ -->
<template>
  <div class="order">
    <van-nav-bar title="ÊàëÁöÑËÆ¢Âçï" left-text="ËøîÂõû" left-arrow @click-left="goback" />
    <van-tabs
      v-model="activeName"
      @change="selectStatus"
      color="rgb(249,187,50)"
      line-height="2"
      title-active-color="rgb(249,187,50)"
    >
      <van-tab :title="item.title" :name="item.name" v-for="(item,index) in tabLists" :key="index">
        
        <div v-if="isHas">
          <van-empty
            class="custom-image"
            image="https://img.yzcdn.cn/vant/custom-empty-image.png"
            description="Âø´ÂéªÈÄõÈÄõÂêßüíó"
          />
        </div>

        <div v-else class="order-content">
          <div class="order-items">

            <div class="order-item" v-for="(item,index) in orderNos" :key="index">

              <div class="order-item-title">

                <div class="fl order-no">{{item}}</div>

                <div class="fr" v-if="orderAllData[item].status == 2">
                  <span class="confirm">Â∑≤Êî∂Ë¥ß</span>
                  <span class="delete">
                    <van-icon class="delete-icon" name="delete" @click="deleteOrder(item)" />
                  </span>
                </div>

                <div class="fr" v-else>
                  <span class="confirm" @click="comfirmProducts(item)">Á°ÆËÆ§Êî∂Ë¥ß</span>
                </div>
              </div>
              <!-- ËÆ¢ÂçïÂïÜÂìÅ -->
              <div class="items" v-for="(v,i) in orderAllData[item].data" :key="i">
                <div class="clearfix">
                  <div class="fl pro-img">
                    <img class="auto-img" :src="v.smallImg" alt />
                  </div>
                  <div class="fl pro-info">
                    <div class="fl">
                      <div class="names">
                        <div class="zh-name">{{v.name}}</div>
                        <div class="en-name">{{v.enname}}</div>
                      </div>
                      <div class="rule">{{v.rule}}</div>
                    </div>
                    <div class="fr">
                      <div class="price">Ôø•{{v.price}}</div>
                      <div class="count">x{{v.count}}</div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="order-total clearfix">
                <div class="date fl">{{orderAllData[item].date | dateFormat}}</div>
                <div class="fr clearfix">
                  <span class="fl total-count">ÂÖ±ËÆ°{{orderAllData[item].count}}‰ª∂ÂïÜÂìÅ ÂêàËÆ°Ôºö</span>
                  <span class="fl total-price">Ôø•{{orderAllData[item].total}}</span>
                </div>
              </div>
            </div>

          </div>
        </div>
        
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../assets/css/order.less";
export default {
  data() {
    return {
      tabLists: [
        {
          title: "ÂÖ®ÈÉ®ËÆ¢Âçï",
          name: "0"
        },
        {
          title: "ÂæÖÊî∂Ë¥ß",
          name: "1"
        },
        {
          title: "Â∑≤Êî∂Ë¥ß",
          name: "2"
        }
      ],
      activeName: "0",
      isRecieve: false,
      orderNos: [], //ËÆ¢ÂçïÁºñÂè∑
      orderAllData: {}, //ËÆ¢ÂçïÂïÜÂìÅ
      isHas:true
    };
  },

  methods: {
    goback(name) {
      this.$router.push({ name: "My" });
    },
    //ÂàáÊç¢Áä∂ÊÄÅ
    selectStatus(a) {
      
      this.getOrder();
    },
    //Ëé∑ÂèñËÆ¢ÂçïÂïÜÂìÅ ÂàáÊç¢Áä∂ÊÄÅ‰πüÈúÄË¶Å‰ΩøÁî®
    getOrder() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.activeName
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 70000) {
            //ÊåâÁÖßËÆ¢ÂçïÁºñÂè∑ÂàÜÁ±ªoids ÂÖàËé∑ÂèñÂêéÂàÜÁ±ª .ËøáÊª§ÈáçÂ§çÁºñÂè∑
            let oids = [];
            result.data.result.forEach(v => {
              if (oids.indexOf(v.oid) === -1) {
                oids.push(v.oid);
                
              }
            });
            this.orderNos = oids; //ËÆ¢ÂçïÂïÜÂìÅÁºñÂè∑
            

            let orderNumber = {};
            result.data.result.forEach(v => {
              if (!orderNumber[v.oid]) {
                //ÁºñÂè∑‰∏çÂ≠òÂú® Â∞± Ê∑ªÂä†Êñ∞ËÆ¢Âçï
                orderNumber[v.oid] = {
                  data: [v],
                  status: v.status, //Áä∂ÊÄÅ
                  date: v.createdAt, //Êó∂Èó¥
                  count: v.count,
                  total: v.price * v.count
                };
              } else {
                orderNumber[v.oid].count += v.count;
                orderNumber[v.oid].total += v.count * v.price;
                orderNumber[v.oid].data.push(v);
              }
            });
            this.orderAllData = orderNumber;
            
            if(this.orderNos.length > 0){
              this.isHas = false;
            }else{
              this.isHas = true;
            }
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //Á°ÆËÆ§Êî∂Ë¥ß ÂàáÊç¢‰∏∫ Â∑≤ÂÆåÊàê
    comfirmProducts(oid) {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 80000) {
            //ÁÇπÂáªÊîπÂèòÁä∂ÊÄÅ‰∏∫2 ÔºåÂ¶ÇÊûúÂæÖÊî∂Ë¥ß Â∞±Âà†Èô§È°µÈù¢ËÆ¢ÂçïÊï∞ÊçÆ
            this.orderAllData[oid].status = 2;
            if (this.activeName == 1) {
              let index = this.orderNos.indexOf(oid);
              this.orderNos.splice(index, 1);
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //Âà†Èô§ÂïÜÂìÅ
    deleteOrder(oid) {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 90000) {
            let index = this.orderNos.indexOf(oid);
            this.orderNos.splice(index, 1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  },
  created() {
    this.getOrder();
  }
};
</script>